<template>
  <div id="Detail">
    <!-- 导航栏 -->
    <detail-nav-bar class="nav-bar" @titleclick="titleclick" ref="nav" />

    <better-scroll class="conten" ref="scroll" @scroll="titlescroll">
      <!-- 轮播图 -->
      <detail-swiper :top-images="topImages" />
      <!-- 商品信息 -->
      <detail-item-info :DetailitemInfo="itemInfo" />
      <!-- 商家信息 -->
      <detail-seller :shop="seller" />
      <!-- 商品穿着图片信息 -->
      <detail-info :detailInfo="detailInfo" @imageload="imageload" />
      <!-- 商品详细参数 -->
      <detail-param-info ref="params" :ParamInfo="itemParams" />
      <!-- 商品评论信息 -->
      <detail-common-info ref="comment" :commonInfo="commonInfo" />
      <!-- 商品推荐信息 -->
      <detail-recommend ref="recommend" :recommend="recommend" />
    </better-scroll>
    <div>
      <toast :message="message" :isShow="Show" />
      <detail-bottom-bar @addToCart="addCart" />
    </div>
    <div>
      <!-- 组件不能使用监听元素事件 这时可以使用native -->
      <back-top @click.native="backClick" v-show="isshow"></back-top>
    </div>
  </div>
</template>

<script>
import DetailNavBar from "./childDetail/DetailNavBar";
import DetailSwiper from "./childDetail/DetailSwiper";
import DetailItemInfo from "./childDetail/DetailItemInfo";
import DetailSeller from "./childDetail/DetailSeller";
import DetailInfo from "./childDetail/DetailInfo";
import DetailParamInfo from "./childDetail/DetailParamInfo";
import DetailCommonInfo from "./childDetail/DetailCommonInfo";
import DetailRecommend from "./childDetail/DetailRecommend";
import DetailBottomBar from "./childDetail/DetailBottomBar";

import BetterScroll from "components/common/scroll/BetterScroll";
import BackTop from "components/content/backtop/BackTop";
import Toast from "components/common/toast/Toast";

import { getdetail, getrecommend } from "network/home";
import { DetailGoods, shopInfo, itemParams } from "network/detail";
import { debounce } from "common/utils";

export default {
  name: "Detail",
  data() {
    return {
      iid: null,
      topImages: [],
      itemInfo: {},
      seller: {},
      detailInfo: {},
      itemParams: {},
      commonInfo: {},
      recommend: [],
      themeTopYs: [],
      getThemeTopY: null,
      isshow: false,
      message: "",
      Show: false,
      currentIndex:0,
    };
  },
  created() {
    console.log("detail-created");
    //获取数据id
    this.iid = this.$route.params.iid;
    console.log(this.$route.params.iid);
    // 请求商品数据
    getdetail(this.iid).then((res) => {
      // console.log(res);
      //获取id的轮播图保存到data数据中
      this.topImages = res.result.itemInfo.topImages;
      const data = res.result;
      //创建商品数据类 获取商品的详细数据
      this.itemInfo = new DetailGoods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      //创建商家数据类
      this.seller = new shopInfo(data.shopInfo);

      //获取商品穿着图片信息
      this.detailInfo = data.detailInfo;

      //获取商品详细参数
      this.itemParams = new itemParams(
        data.itemParams.info,
        data.itemParams.rule
      );

      //获取商品评论信息
      if (data.rate.cRate != 0) {
        this.commonInfo = data.rate.list[0];
      }

      //请求推荐数据
      getrecommend().then((res) => {
        this.recommend = res.data.list;
      });
      this.getThemeTopY = debounce(() => {
        this.themeTopYs = [];
        this.themeTopYs.push(0);
        this.themeTopYs.push(this.$refs.params.$el.offsetTop - 44);
        this.themeTopYs.push(this.$refs.comment.$el.offsetTop - 44);
        this.themeTopYs.push(this.$refs.recommend.$el.offsetTop - 44);
        this.themeTopYs.push(Number.MAX_VALUE);
        console.log(this.themeTopYs);
      });
    });
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailItemInfo,
    DetailSeller,
    BetterScroll,
    DetailInfo,
    DetailParamInfo,
    DetailCommonInfo,
    DetailRecommend,
    BackTop,
    DetailBottomBar,
    Toast,
  },
  mounted() {},
  updated() {},
  methods: {
    imageload() {
      this.$refs.scroll.scroll.refresh();
      this.getThemeTopY();
    },
    titleclick(index) {
      console.log(index);
      this.$refs.scroll.scroll.scrollTo(0, -this.themeTopYs[index], 260);
    },
    backClick() {
      this.$refs.scroll.scroll.scrollTo(0, 0, 500);
    },
    titlescroll(position) {
      // 返回顶部按钮的显示隐藏
      this.isshow = position.y < -1000 ? true : false;
      //获取y值
      const positionY = -position.y;
      //获取导航栏的长度
      let length = this.themeTopYs.length

      for(let i = 0; i <length-1; i++){
        if(this.currentIndex !== i && (positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i+1])){
          this.currentIndex = i
          this.$refs.nav.currentIndex = this.currentIndex
        }
      }
    },
    
    addCart() {
      //获取购物车信息
      const product = {};
      product.Images = this.topImages[0];
      product.title = this.itemInfo.title;
      product.desc = this.itemInfo.desc;
      product.lowNowPrice = this.itemInfo.lowNowPrice;
      product.iid = this.iid;
      console.log(this.itemInfo);
      //将商品添加到购物车里面
      // this.$store.commit('addCart',product)
      this.$store.dispatch("addCart", product).then((res) => {
        this.message = res;
        this.Show = true;
        setTimeout(() => {
          this.Show = false;
        }, 2000);
      });
    },
  },
};
</script>

<style scoped>
#Detail {
  position: relative;
  z-index: 9;
  background-color: #fff;
  height: 100vh;
}
.conten {
  height: calc(100% - 49px);
}
.nav-bar {
  position: relative;
  top: 0;
  background-color: #fff;
  z-index: 9;
}
</style>