<template>
  <div id="DetailInfo" v-if="Object.keys(detailInfo).length!=0">
    <div class="start"></div>
    <div class="detailInfo-name">{{detailInfo.desc}}</div>
    <div class="end"></div>
    <div class="detailImage-key">{{detailInfo.detailImage[0].key}}</div>
    <div v-for="item in detailInfo.detailImage[0].list" :key="item" class="detailImage" >
      <img :src="item" alt="" @load="imageload"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailInfo",
  props: {
    detailInfo: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {};
  },
  components: {},
  mounted() {},
  methods: {
      imageload(){
          this.$emit('imageload')
      }
  },
};
</script>

<style scoped>
.detailInfo-name {
  font-size: 14px;
  margin-top: 10px;
  padding: 10px;
}
.start,
.end {
  width: 90px;
  height: 1px;
  background-color: #a3a3a5;
  position: relative;
}
.start {
  float: left;
  left: 10px;
}
.end {
  float: right;
  right: 10px;
}
.start::before,
.end::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #333;
  bottom: 0;
}
.end::after {
  right: 0;
}

.detailImage-key {
  margin-left: 10px;
  font-weight: bolder;
}
.detailImage img {
  width: 100%;
}
</style>