<template>
  <div id="DetailNavBar">
      <nav-bar class="home-nav">
          <div slot="left" class="backimg" @click="backclick">
              <img src="~assets/img/common/back.svg" alt="">
          </div>
          <div slot="center" class="title">
            <div v-for="(item,index) in Detail" :key="item" class="control"
            :class="{active: index === currentIndex}"
            @click="titleckick(index)" >
            {{Detail[index]}}</div>
            </div>
      </nav-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";

export default {
  name: 'DetailNavBar',
  data () {
    return {
      Detail:['商品','参数','评论','推荐'],
      currentIndex:0
    };
  },
  components: {
      NavBar
  },
  mounted () {},
  methods: {
      titleckick(index){
        // console.log(index);
         this.currentIndex = index
         this.$emit('titleclick',index)
      },
      backclick(){
        //go(-1) back() 2种都可以返回
        //this.$router.go(-1)
          this.$router.back()
      }
  }
}
</script>

<style scoped>
.title{
    display: flex;
    font-size: 14px;
    text-align: center;
}
.control{
    flex: 1;
}
.active{
    color: var(--color-high-text)
}
.backimg{
    text-align: center;
    margin-top: 6px;
}

</style>