      <tr v-for="item in newtab" :key="item.index" class="tr">
        <td v-for="td in item" :key="td.index" class="td">{{td}}</td>
      </tr>
    </table>

    <table class="table1">
      <tr v-for="item in ParamInfo.set" :key="item.index" class="tr1">
        <td class="td-key td1">{{item.key}}</td>
        <td class="td-value td1">{{item.value}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "DetailParamInfo",
  props: {
    ParamInfo: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
